.navbar-container {
  display: none;
  width: 10%;

  .navbar {
    // background-color: #060b26;
    // height: 80px;
    // display: flex;
    // justify-content: start;
    // align-items: center;
    .menu-bars {
      position: absolute;
      top: 25px;
      left: 5%;
    }
  }

  .menu-bars {
    // margin-left: 2rem;
    font-size: 2rem;
    background: none;
  }

  .menu-bars svg {
    color: black !important;
  }

  .nav-menu {
    background-color: #f9f9f9;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    z-index: 1;
    box-shadow: 1px 0px 6px -2px black;
  }

  .nav-menu.active {
    left: 0;
    transition: 350ms;
  }

  .nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
  }

  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
  }

  .nav-text a:hover {
    background-color: #1a83ff;
  }

  .nav-menu-items {
    width: 100%;
  }

  .navbar-toggle {
    width: 100%;
    height: 80px;
    display: block;
    text-align: right;
    padding-right: 20px;
    padding-top: 25px;
  }

  .navbar-btns {
    height: calc(100% - 80px);
    display: block;
  }

  .navbar-button {
    display: block;
    width: 100%;
    border-bottom: 1px solid #0000000d;
    height: calc(100% / 7);
  }

  .navbar-button:first-child {
    border-top: 1px solid #0000000d;
  }

  .navbar-button a {
    width: 100%;
    font-weight: bold;
    text-align: center;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-content: space-between;
    justify-content: space-around;
  }
}

@media only screen and (max-width: 961px) {
  .navbar-container {
    display: block;

    .navbar-button a {
      font-size: 20px;
    }
  }
}

@media only screen and (max-width: 641px) {
  .navbar-container {
    .navbar-button a {
      font-size: 16px;
    }
  }
}
