.header {
  width: 100%;
  height: 80px;
  border-bottom: 2px solid black;
}

.header-container {
  width: auto;
  margin-left: 70px;
  margin-right: 70px;
  padding-top: 25px;
}

.header-title {
  width: 20%;
  font-size: 20px;
  font-weight: 400;
  display: inline-block;
  color: black;
  pointer-events: none;
}

.header-btns {
  width: 70%;
  display: inline-block;
}

.header-btns span {
  width: 14%;
  display: inline-block;
  text-align: center;
  color: black;
  font-weight: 400;
}

.header-btns a:hover {
  color: #2d476d !important;
  text-decoration: underline;
}

.header-theme {
  width: 10%;
  display: inline-block;
}

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 45px;
  height: 22px;
  top: 5px;
  float: right;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: white;
  border: 2px solid black;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 30px;
  width: 30px;
  left: 0px;
  bottom: 4px;
  top: 0;
  bottom: 0;
  margin: auto 0px auto -5px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  box-shadow: 0 0px 15px #2020203d;
  background: url("../images/sun-black-img.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px;
  border: 2px solid black;
  background-color: white;
}

input:checked + .slider {
  background-color: black;
  border: 2px solid white;
}

input:focus + .slider {
  box-shadow: 0 0 1px black;
}

input:checked + .slider:before {
  -webkit-transform: translateX(24px);
  -ms-transform: translateX(24px);
  transform: translateX(24px);
  background: url("../images/moon-white-img.png");
  background-repeat: no-repeat;
  background-position: center;
  border: 2px solid white;
  background-size: 20px;
  background-color: black;
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

