// Media Queries
@media only screen and (max-width: 1500px) and (min-width: 1201px) {
  .header-btns span {
    font-size: 13px;
  }
}

@media only screen and (max-width: 1200px) {
  .header-btns span {
    font-size: 12px;
  }
}

@media only screen and (max-width: 961px) {
  .header-btns {
    display: none;
  }

  .header-container {
    margin-left: 15%;
    margin-right: 5%;
    padding-top: 25px;
  }

  .header-title {
    width: 90%;
    text-align: center;
  }

