.footer {
  width: 100%;
  bottom: 0;
  position: fixed;
}

.footer-container {
  width: 70%;
  margin-left: auto;
  padding-top: 34px;
  padding-bottom: 30px;
  padding-left: 40px;
  background-color: #2d476d;
  color: white;
  border-top-left-radius: 10px;
  box-shadow: 0px 0px 5px -2px black;
}

.footer-text-section {
  width: 40%;
}

.footer-text {
  font-size: 13px;
}

@media only screen and (max-width: 1025px) {
  .footer {
    // height: 100px;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
  }

  .footer-container {
    width: 100%;
    border-top-left-radius: 0;
    padding-left: 0;
  }

  .footer-text-section {
    width: 100%;
    text-align: center;
  }
}
